import React from 'react'
import styled from 'styled-components'
import {
  EditOutlined,
  DeleteOutlined,
  // RightOutlined,
  // LeftOutlined,
} from '@ant-design/icons'
import cx from 'classnames'
import { Tooltip } from 'antd'

type Props = {
  tabIndex: number
  onEdit: () => void
  onDelete: () => void
}

const ToolbarWrapper = styled.div`
  position: absolute;
  top: 16px;
  right: 24px;
  visibility: hidden;
  transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
  opacity: 0;
  z-index: 1;

  &.is--over {
    visibility: visible;
    transition: visibility 0s, opacity 0.3s, max-height 0.6s linear;
    opacity: 0.8;
  }
`

export default function SnippetToolbar({ tabIndex, onEdit, onDelete }: Props) {
  return (
    <ToolbarWrapper
      className={cx('text-primary', { 'is--over': tabIndex === 1 })}>
      {/* <Tooltip title="向左縮排">
        <LeftOutlined className="mr-2 cursor-pointer" />
      </Tooltip>
      <Tooltip title="向右縮排">
        <RightOutlined className="mr-2 cursor-pointer" />
      </Tooltip> */}
      <Tooltip title="編輯">
        <EditOutlined className="mr-2 cursor-pointer" onClick={onEdit} />
      </Tooltip>
      <Tooltip title="刪除">
        <DeleteOutlined className="cursor-pointer" onClick={onDelete} />
      </Tooltip>
    </ToolbarWrapper>
  )
}
