import React from 'react'
import { DragSource, ConnectDragPreview, ConnectDragSource } from 'react-dnd'
import { getEmptyImage } from 'react-dnd-html5-backend'
import { mergeRight } from 'ramda'
import cx from 'classnames'
import { confirm } from '@/utils/message'
import ItemTypes from '@/constants/item-types'
import SnippetToolbar from './snippet-toolbar'
import theme from '../theme'

type Props = {
  id?: React.Key
  wrapperId?: string
  className?: string
  style?: React.CSSProperties
  index?: number
  isDragging?: boolean
  draggable?: boolean
  fromMenu?: boolean
  connectDragPreview: ConnectDragPreview
  connectDragSource: ConnectDragSource
  children?: React.ReactNode
  findSnippet?(id: React.Key): any
  removeSnippet?(index: number): void
}

type State = {
  top: number
  tabIndex: number
  editModalVisible: boolean
}

const withDragSource = DragSource(
  ItemTypes.BOARD,
  {
    beginDrag(props: Props) {
      return {
        ...props,
        fromMenu: false,
        originalIndex: (props as any).findSnippet(props.id).index,
      }
    },
    canDrag(props: Props) {
      return !!(props.draggable && !props.fromMenu)
    },
  },
  (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    connectDragPreview: connect.dragPreview(),
    isDragging: monitor.isDragging(),
  })
)

class Snippet extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props)

    this.state = {
      top: 0,
      tabIndex: -1,
      editModalVisible: false,
    }
  }

  componentDidMount = () => {
    const { connectDragPreview } = this.props
    if (connectDragPreview) {
      connectDragPreview(getEmptyImage() /* { anchorX: 0.2, anchorY: 0.1 } */)
    }
  }

  handleRemove = () => {
    confirm({
      content: '刪除後將無法還原，確認繼續？',
      title: '確認要刪除這個模版?',
      onOk: () => {
        ;(this.props as any).removeSnippet(this.props.index)
      },
      onCancel() {
        console.log('Cancel')
      },
    })
  }

  handleEditModalVisible = () => {
    this.setState(prev => ({ editModalVisible: !prev.editModalVisible }))
  }

  handleIndent = (align: 'left' | 'right') => () => {}

  _onMouseOver = () => {
    this.setState({ tabIndex: 1 })
  }

  _onMouseOut = () => {
    this.setState({ tabIndex: -1 })
  }

  render() {
    const {
      props: {
        connectDragSource,
        connectDragPreview,
        isDragging,
        wrapperId,
        index,
        draggable,
        style = {},
        className = 'mb-3',
      },
      state: { tabIndex, editModalVisible },
    } = this

    const styles = mergeRight(
      getItemStyles({ isDragging, tabIndex, draggable }),
      style
    )

    return connectDragSource(
      connectDragPreview(
        <div
          id={wrapperId}
          style={styles}
          className={cx(
            'ui-sortable',
            { 'ui-draggable': draggable },
            className
          )}
          tabIndex={tabIndex}
          onMouseOver={this._onMouseOver}
          onMouseOut={this._onMouseOut}
          data-index={index}>
          {draggable && (
            <SnippetToolbar
              tabIndex={tabIndex}
              onEdit={this.handleEditModalVisible}
              onDelete={this.handleRemove}
            />
          )}
          {React.Children.map(this.props.children, child =>
            React.cloneElement(child as any, {
              editModalVisible,
              handleEditModalVisible: this.handleEditModalVisible,
            })
          )}
        </div>
      )
    )
  }
}

function getItemStyles(props: Partial<Props & State>): React.CSSProperties {
  const { isDragging, tabIndex, draggable } = props

  const isFocus = tabIndex === 1

  return {
    ...(isFocus ? { borderRadius: 3 } : {}),
    ...(draggable
      ? { border: `1px dashed ${draggable ? '#e7e7e7' : 'transparent'}` }
      : {
          outlineColor: isFocus ? theme.purple150 : 'transparent',
        }),
    cursor: 'default',
    position: 'relative',
    display: isDragging ? 'none' : 'block',
  }
}

export default withDragSource(Snippet)
