import React from 'react'
import { ModalProps } from 'antd/lib/modal'
import { length, __, gt, keys, map, pathOr } from 'ramda'
import Modal from '@/components/modal'
import Snippet from '@/components/snippet'
import { mapIndexed } from '@/utils/webHelper'
const { compose } = require('ramda')

interface Props extends ModalProps {
  snippetList?: { [key: string]: any[] }
}

export default function SnippetPreviewModal({ onCancel, snippetList }: Props) {
  return (
    <Modal
      visible
      style={{ top: 30 }}
      maskClosable={false}
      className="ant-modal-header--border-none"
      keyboard={false}
      width="85%"
      onCancel={onCancel}
      footer={false}>
      {compose(
        map((key: string) => (
          <div key={key}>
            {compose(gt(__, 0), length, pathOr([], [key]))(snippetList) && (
              <h2 className="my-2">{key}</h2>
            )}
            {compose(
              mapIndexed((item: any, idx: number) => (
                <Snippet key={item.id} draggable={false}>
                  <item.Component
                    id={item.id}
                    className={item.id}
                    item={item}
                    index={idx}
                  />
                </Snippet>
              )),
              pathOr([], [key])
            )(snippetList)}
          </div>
        )),
        keys
      )(snippetList)}
    </Modal>
  )
}
